









































































































































































#tabletRegister{
  width: 100%;
  height: 100%;
  background: url("../../assets/circlesMark_images/tableMove_back.jpg") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  img{
    width: 5.5rem;
    margin-top: 1.95rem;
  }
  ul{
    margin-top: 1.62rem;
    color:#fff;
    li{
      font-size: 0.28rem;
      line-height: 0.54rem;
    }
  }
  button{
    background: url("../../assets/circlesMark_images/tableMove_btn.png");
    background-size: 100% 100%;
    border: 0;
    padding: 0.19rem 0.43rem;
    color:#fff;
    font-size: 0.28rem;
    font-weight: bolder;
    outline: none;
    margin-top: 0.86rem;
    cursor: pointer;
    box-shadow: 0 0.1rem 4px 0px rgba(0, 0, 0, 0.1);
  }
}
